/* Customize the label (the container) */
.custom-checkbox {
  display: block;
  position: relative;
  cursor: pointer;
  letter-spacing: 0.3px;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 0;
  box-shadow: 0 2px 6.5px 0 #e0e0e0;
  height: 70px;
  border-radius: 10px;
  margin-left: 13px;
  margin-right: 13px;
  padding-top: 20px;
  padding-left: 20px;
}

.custom-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Hide the browser's default checkbox */
.custom-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 20px;
  right: 10px;
  height: 30px;
  width: 30px;
  border: 1px solid #C4C4C4;
  border-radius: 50%;
  background-color: #FFFFFF;
}

/* On mouse-over, add a grey background color */
/* When the checkbox is checked, add a blue background */
.custom-checkbox input:checked ~ .checkmark {
  background-color: rgba(61, 211, 166, 0.3);
  border: none;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.custom-checkbox input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.custom-checkbox .checkmark:after {
  left: 11px;
  top: 5px;
  width: 8px;
  height: 15px;
  border: solid #3dd3a6;
  border-radius: 2px;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

